.timetable__time-slots {
  margin-top: 45px;
  background-color: white;
  z-index: 1; }

.timetable__events {
  z-index: 0; }

.timetable__content {
  display: -ms-flexbox;
  display: flex; }

.timetable__event-info {
  width: 100%;
  max-width: 600px; }

.timetable__go-to-now {
  position: fixed;
  bottom: 30px;
  right: 30px; }

@supports ((position: -webkit-sticky) or (position: sticky)) {
  .timetable__time-slots {
    position: -webkit-sticky;
    position: sticky;
    left: 0; } }



